*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    background: #f5f7fa;
    color: #333;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

/* Header content */
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 2rem;
    background: #ffffff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    border-radius: 0 0 10px 10px;
}

.location-name-container {
    font-size: 2.7rem;
    font-weight: 700;
    color: #007bff;
    user-select: none;
}

.search-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.3rem;
}

.toggle {
    align-self: flex-end;
    margin: 0 10px;
    padding: 5px 15px;
}

#search-location-weather {
    display: flex;
}

.location-searchbar {
    padding: 0.6rem 1rem;
    font-size: 1rem;
    border: 2px solid #ddd;
    border-radius: 25px;
    width: 220px;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.location-searchbar:focus {
    border-color: #007bff;
    box-shadow: 0 0 6px rgba(0, 123, 255, 0.4);
    outline: none;
}

/* Slider */

.toggle-container {
    display: flex;
    align-items: center;
    font-family: sans-serif;
}

.toggle {
    position: relative;
    width: 60px;
    height: 30px;
    margin: 0 10px;
}

.toggle input {
    display: none;
}

.slider {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #007bff;
    border-radius: 30px;
    cursor: pointer;
    transition: 0.3s;
}

.slider:before {
    content: "";
    position: absolute;
    height: 24px;
    width: 24px;
    left: 3px;
    top: 3px;
    background-color: white;
    border-radius: 50%;
    transition: 0.3s;
}

input:checked + .slider {
    background-color: rgba(255, 0, 0, 0.664);
}

input:checked + .slider:before {
    transform: translateX(30px);
}

/* Main content */
main {
    flex-grow: 1;
    width: 100%;
    max-width: 1200px;
    margin: 2rem auto;
    padding: 0 1rem;
    display: flex;
    gap: 3rem;
    justify-content: center;
}

.today-weather,
.future-weather {
    background: #ffffff;
    box-shadow: 0 2px 12px rgb(0 0 0 / 0.1);
    border-radius: 12px;
    padding: 2rem;
    flex: 1 1 300px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    width: 100%;
}

.today-weather .weather-icon-container,
.future-weather .weather-icon-container {
    width: 120px;
    height: 120px;
}

.weather-icon-container img {
    max-width: 100%;
    height: auto;
}

.content-text {
    font-size: 2rem;
    font-weight: 600;
    overflow: auto;
    word-wrap: break-word;
}

.weekly-data {
    max-width: 1200px;
    margin: 0 auto 3rem;
    padding: 0.5rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 1rem;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.8);
    width: 100%;
    min-height: 16vh;
    box-shadow: 0 2px 12px rgb(0 0 0 / 0.1);
    border-radius: 12px;
}

.day-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    box-shadow: 0 2px 12px rgb(0 0 0 / 0.1);
    background-color: #fff;
}

.week-temp-text {
    font-size: 1.2rem;
    font-weight: 600;
    margin: 0;
}

@media (max-width: 768px) {
    header {
        flex-direction: column;
    }

    main {
        flex-direction: column;
        gap: 2rem;
        scroll-behavior: auto;
        font-size: 0.8rem;
    }

    .content-text {
        font-size: 1rem;
        overflow: auto;
        word-wrap: break-word;
    }

    .location-header {
        flex-direction: column;
        align-items: flex-start;
        padding: 1rem 1.5rem;
        border-radius: 0 0 0 0;
    }

    .location-name-container {
        font-size: 1.8rem;
        margin-bottom: 1rem;
    }

    .location-searchbar {
        width: 100%;
    }

    .today-weather .weather-icon-container,
    .future-weather .weather-icon-container {
        width: 80px;
        height: 80px;
    }

    .weather-icon-container img {
        width: 100%;
        height: 100%;
    }

    .today-weather,
    .future-weather {
        flex: none;
        width: 100%;
        padding: 0.8rem;
    }
}
